
// @ts-ignore
import ReactStars from "react-rating-stars-component";

const truncate =( str:string) =>{
  return str.length > 20 ? str.substring(0, 15) + "..." : str;
}

const product = ({ item } :any) => {
  const updatedPrice =(sales:string,price:string)=>{
    const priceCurrent:number=Number(price);
    const saleCurrent:number= -parseFloat(sales)/100;
     if (sales.includes("%")){
       return <p>${Math.round( priceCurrent -(saleCurrent*priceCurrent))}</p>
     }else{
       return <p>${Math.round(priceCurrent - saleCurrent*100)}</p>
     }
  }
  
  return(
    <div className="product-item">
      <div className="content-left">
        <img  src={item.cover} alt=""/>
      </div>
      <div className="content_right">
        <h5 style={{overflow: 'hidden', textOverflow: 'ellipsis'}} > {truncate(item.name)}</h5>
        <ReactStars
          count={5}
          size={24}
          isHalf={false}
          edit={false}
          value ={item.stars}
          emptyIcon={<i className="far fa-star"></i>}
          fullIcon={<i className="fa fa-star"></i>}
          activeColor="#fbbc05"
        />
        <p className="price">
          <span className= {item.sales !== "0" ? "price-edit" : "old-price"} >${item.price}</span>
          <span className="current-price">{item.sales === "0" ? null : updatedPrice(item.sales,item.price)}</span>
          </p>
      </div>
  </div>
  )
};

export default product;