import React, {useState} from 'react';
import logo from '../../../asset/logo-header.jpg';

const Header=()=> {
    const Pages=[
        { id:"1", name:"Home", URL:""},
        {id:"2",name:"Fresh Food",URL:""},
				{id:"3",name:"Fresh Vegetables",URL:"",
				subMenuList:[
					{id:"2.1",name:"Potatoes & Sweet Potatoes", URL:""},
					{id:"2.2",name:"Onions & Leeks", URL:""},
					{id:"2.3",name:"Carrots & Root Vegetables", URL:""},
					{id:"2.4",name:"Broccoli & Cauliflower", URL:""},
					{id:"2.5",name:"Cabbages & Greens", URL:""},
				]
			},
        {id:"4",name:"Price Drops",URL:""},
        {id:"5",name:"Contact Us",URL:""},

    ]
    const [idMenu, setIdMenu] = useState("");
    const showSubMenu = (id:string) => {
        setIdMenu(id);
    }
    const hideSubMenu = (id:string) => {
        setIdMenu("null");
    }
 
    return (
        <>
      <div className="Header">
				<div className="logo-header">
				<img className="img-responsive" src={logo} alt="logo"/>
				</div>
				<div className="menu-list">
						<ul>
						{
						Pages.map((page)=>
						<li>
						<li key={page.id} onMouseLeave={() =>{hideSubMenu(page.id)}} onMouseEnter={()=>{showSubMenu(page.id)}}>{page.name}</li>
							{page.id == idMenu && page.subMenuList && <ul className="sub-list">{page.subMenuList.map(subItem => (
							<li key= {subItem.id}>{subItem.name}</li>
							))}</ul>
							}    
						</li>
						)}
						</ul>
				</div>
      </div>
			<div className="banner"></div>
      </>
    );
};
  export default Header;
